
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
}
.nav-bar {
  height: 100px;
  width: 100%;
  background-color: #31404f;
}
.logo {
  float: left;
}
.logo img {
  width: 80px;
  height: 80px;
  padding: 10px;
}
.menu {
  height: 100%;
  /*width: 50%;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  float: left;
}
.menu li a{
  text-decoration: none;
  color: #fff;
  font-size: 18px;
}
.search-bar {
  float: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100px;
  width: 200px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.search-bar input {
  font-size: 16px;
  margin: 20px;
  padding: 5px;
}
.my {
  float: right;
}
.my img {
  width: 60px;
  height: 60px;
  padding: 20px;
  border-radius: 50%;
}



